import React, { useState } from "react";
import ProjectItem from "./ProjectItem/ProjectItem";

//functionality-aesthethic imports
import ReactDelayRender from "react-delay-render";
import FadeIn from "react-fade-in";
import Collapsible from "react-collapsible";

// import kolorkiImg from "../../../../../assets/projects/kolorki.PNG";
import budgetImg from "../../../../../assets/projects/BudgControl.PNG";
import tdeeCalcImg from "../../../../../assets/projects/tdeeRedo.PNG";
import forkifyImg from "../../../../../assets/projects/forkifySmall.PNG";
import burgerImg from "../../../../../assets/projects/burgerBuilder.png";
import pokedexImg from "../../../../../assets/projects/pokedex.PNG";
import hirehiveImg from "../../../../../assets/projects/hirehive.PNG";
import tdeeFit from "../../../../../assets/projects/tdeefit.PNG";

import "./Projects.css";

const Projects = () => {
  const [mobile] = useState(window.innerWidth <= 768);

  const [tabOpen, setTabOpen] = useState({
    proj1: false,
    proj2: false,
    proj3: false,
    proj4: false,
    proj5: false,
    proj6: false,
    proj7: false,
    proj8: false
  });

  // one open at a time on mobile, many can be opened on desktop
  const triggerClicked = tab => {
    if (mobile) {
      setTabOpen({ [tab]: !tabOpen[tab] });
    } else {
      setTabOpen({ [tab]: !tabOpen[tab] });
      // Below version allows for multiple opening of projects which is no longer feasible due to amount of them
      // setTabOpen({ ...tabOpen, [tab]: !tabOpen[tab] });
    }
  };

  return (
    <FadeIn>
      <div className="projects__wrapper">
        <div className="projects__list">
          {/* <Collapsible
            handleTriggerClick={() => triggerClicked("proj1")}
            open={tabOpen["proj1"]}
            trigger="Color Game"
          >
            <ProjectItem
              triggerTitle="Color Game"
              hrefLink="https://filipzakrocki.github.io/colorGame/"
              img={kolorkiImg}
            >
              Simple DOM manipulation game where the user is challenged to guess
              the color by the RGB values.
            </ProjectItem>
          </Collapsible> */}
          {/* <Collapsible
            handleTriggerClick={() => triggerClicked("proj2")}
            open={tabOpen["proj2"]}
            trigger="Budget Control"
          >
            <ProjectItem
              hrefLink="https://filipzakrocki.github.io/BudgControl/"
              img={budgetImg}
            >
              Simple app allowing you to manage your budget and check the
              percentage of each expense.
            </ProjectItem>
          </Collapsible>
          <Collapsible
            handleTriggerClick={() => triggerClicked("proj3")}
            open={tabOpen["proj3"]}
            trigger="TDEE Calculator"
          >
            <ProjectItem
              hrefLink="https://filipzakrocki.github.io/TDEEc/"
              img={tdeeCalcImg}
            >
              TDEE (Total Daily Energy Expenditure) app calculating your daily
              caloric requirements and recommending changes for weight loss
              based on 2 weeks of data input. Uses localStorage for persisting
              data.
            </ProjectItem>
          </Collapsible> */}
          <Collapsible
            handleTriggerClick={() => triggerClicked("proj4")}
            open={tabOpen["proj4"]}
            trigger="Forkify (API down - looking for alt)"
          >
            <ProjectItem
              hrefLink="https://filipzakrocki.github.io/forkify/"
              img={forkifyImg}
            >
              App using the Food2Fork API () that allows you to look for
              recipes, save them as favorites and prepare shopping lists. Coded
              as a project with Jonas Schmedtimann as a part of his Udemy
              Course.
            </ProjectItem>
          </Collapsible>
          <Collapsible
            handleTriggerClick={() => triggerClicked("proj5")}
            open={tabOpen["proj5"]}
            trigger="Burger Builder"
          >
            <ProjectItem
              hrefLink={"https://filipzakrocki.github.io/burgerBuilder/#/"}
              img={burgerImg}
            >
              Build your own burger with this simple, mobile-friendly
              React+Redux App - orders and authentication are stored on Firebase
              server.
            </ProjectItem>
          </Collapsible>
          <Collapsible
            handleTriggerClick={() => triggerClicked("proj6")}
            open={tabOpen["proj6"]}
            trigger="HireHive application"
          >
            <ProjectItem
              hrefLink={"https://filipzakrocki.github.io/hirehive"}
              img={hirehiveImg}
            >
              Job search engine. Search by keyword, location or job title and
              #hashtags. Assignment for HireHive startup, company offering
              Recruitment SaaS solutions.
            </ProjectItem>
          </Collapsible>
          <Collapsible
            handleTriggerClick={() => triggerClicked("proj7")}
            open={tabOpen["proj7"]}
            trigger="Pokemon TCG Pokedex"
          >
            <ProjectItem
              hrefLink={"https://filipzakrocki.github.io/pokedex"}
              img={pokedexImg}
            >
              Browse through a large collection of Pokemon Trading Card Game.
              React.js + Redux + Thunk. Add additional criteria in the
              searchbar!
            </ProjectItem>
          </Collapsible>
          <Collapsible
            handleTriggerClick={() => triggerClicked("proj8")}
            open={tabOpen["proj8"]}
            trigger="Tdee.fit (Work in Progress)"
          >
            <ProjectItem hrefLink={"https://tdee.fit"} img={tdeeFit}>
              Calculate your Total Daily Energy Expenditure with this tool by
              entering daily weight and caloric consumption. Features local and
              server saving, Firebase Database and authentication and CI/CD with
              Netlify. Constructed with React + Redux!
            </ProjectItem>
          </Collapsible>
        </div>
      </div>
    </FadeIn>
  );
};

export default ReactDelayRender({ delay: 401 })(Projects);
